export default{
	data(){
		return{
			showVideo:true,
			videoSrc:"",
			videoContext:"",
			ratingsList:[],
			labelIndex:0,//当前评论的下标
			labelList:[
				{
						name: '全部',
						number: 25,
						type: 'all'
					},
					{
						name: '好评',
						number: 23,
						type: 'good'
					},
					{
						name: '中评',
						number: 1,
						type: 'secondary'
					},
					{
						name: '差评',
						number: 1,
						type: 'poor'
					},
					{
						name: '有图',
						number: 12,
						type: 'img'
					},
					{
						name: '视频',
						number: 2,
						type: 'video'
					},
					{
						name: '追加',
						number: 2,
						type: 'append'
					}
			]
		}
	},
	onLoad(){

		this.showVideo = false

		try{
			const comments = uni.getStorageSync('comments')
			if(comments){
				this.ratingsList = comments
				//console.log(this.ratingsList)
			}
		}catch(e){
			console.log(e)
		}
	},
	methods:{
		loadRatings(index,label){
				this.labelIndex=index
				const comments = uni.getStorageSync('comments')
				if(comments){
					this.ratingsList = comments
				}

				var a = this.ratingsList.find(item => item.grade == label.type)
				if(a == undefined){
					return
				}
				this.ratingsList = []
				this.ratingsList.push(a)
				
			},
		showBigImg(image,images){
			uni.previewImage({
				current:image,
				urls:images
			})
		},
		playVideo(path){
			//console.log(path)
			this.videoSrc = path
			
			//全屏播放
			this.$nextTick(()=>{
				this.videoContext.requestFullScreen({
					direction:0
			})
			})
		},
		viideoControl(e){
			//console.log(e)
			if(e.detail.fullScreen){//全屏
				//自动播放
				this.videoContext.play()
			}else{
				//停止播放
				//this.videoContext.pause()
				this.videoPause()
			}
		},
		videoPause(){
			this.videoSrc=""
		}
	},
	onReady(){
		this.videoContext = uni.createVideoContext('myVideo')
	}
}
